:root {
  --main-ff: "RFDewiExtended", sans-serif;
  --color-dark: #2d3040;
  --color-white: #fff;
  --color-orange: #ef7d00;
  --color-orange-dark: #DA7200;
  --color-orange-light: #ffe9ce;
  --color-purple-light: #f9f9ff;
  --color-comet: #5C6073;
  --color-dark-blue: #2E3458;
  --color-darker-blue: #242945;
  --color-grey: #C6C6C6;
  --color-grey3: #ADB0C3;
}

@font-face {
  font-weight: 400;
  font-family: "RFDewiExtended";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RFDewiExtended-Regular.woff2") format("woff2"), url("../fonts/RFDewiExtended-Regular.woff") format("woff");
}

@font-face {
  font-weight: 600;
  font-family: "RFDewiExtended";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RFDewiExtended-Semibold.woff2") format("woff2"), url("../fonts/RFDewiExtended-Semibold.woff") format("woff");
}

@font-face {
  font-weight: 700;
  font-family: "RFDewiExtended";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RFDewiExtended-Bold.woff2") format("woff2"), url("../fonts/RFDewiExtended-Bold.woff") format("woff");
}

@font-face {
  font-weight: 800;
  font-family: "RFDewiExtended";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RFDewiExtended-Ultrabold.woff2") format("woff2"), url("../fonts/RFDewiExtended-Ultrabold.woff") format("woff");
}

@font-face {
  font-weight: 900;
  font-family: "RFDewiExtended";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RFDewiExtended-Black.woff2") format("woff2"), url("../fonts/RFDewiExtended-Black.woff") format("woff");
}

.button-reset {
  padding: 0;
  margin: 0;
  background-color: transparent;
  border: none;
  border-radius: 0;
}

.logo__img {
  display: block;
  width: 30rem;
}

@media screen and (max-width: 767px) {
  .logo__img {
    max-width: 15.4rem;
  }
}

.nav__list {
  display: flex;
  align-items: center;
}

.nav__item {
  margin-right: 2.8rem;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  position: relative;
  display: inline-block;
  overflow: hidden;
  font-size: 2rem;
  line-height: 3.8rem;
  transition: color 200ms;
}

.nav__link:hover::before {
  transform: translateX(0);
}

.nav__link::before {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: var(--color-dark);
  transform: translateX(-100%);
  transition: transform 200ms;
  content: "";
}

.nav__link.is-current::before {
  transform: translateX(0);
}

.button-burger {
  position: relative;
  z-index: 15;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 6.5rem;
  height: 6.5rem;
  cursor: pointer;
}

.button-burger span {
  display: block;
  margin-bottom: 1rem;
  width: 5.7rem;
  height: 2px;
  background-color: var(--color-dark);
  transition: background-color 200ms, transform 200ms;
}

.button-burger span:last-child {
  margin-bottom: 0;
}

.button-burger:not(.is-active):hover span:nth-child(1) {
  transform: translateY(0.3rem);
}

.button-burger:not(.is-active):hover span:nth-child(2) {
  transform: translateY(-0.3rem);
}

.button-burger:active {
  transform: scale(0.99);
}

.button-burger.is-active span {
  background-color: var(--color-white);
  margin: 0;
}

.button-burger.is-active span:nth-child(1) {
  transform: rotate(32deg);
}

.button-burger.is-active span:nth-child(2) {
  transform: rotate(-32deg);
}

.tel__link {
  display: inline-block;
  font-weight: 800;
  font-size: 2.2rem;
  line-height: 3.6rem;
  transition: color 200ms;
}

.tel__link:hover {
  color: var(--color-orange);
}

.tel__text {
  display: block;
  font-size: 1.3rem;
  line-height: 1.6rem;
  color: #959FAB;
}

.section-name {
  display: block;
  font-weight: 600;
  font-size: 2.4rem;
  line-height: 2.9rem;
  color: rgba(45, 48, 64, 0.4);
}

@media screen and (max-width: 1199px) {
  .section-name {
    font-size: 2rem;
    line-height: 2.4rem;
  }
}

@media screen and (max-width: 767px) {
  .section-name {
    font-size: 1.4rem;
    line-height: 1.6rem;
  }
}

.site-second-title {
  font-weight: 800;
  font-size: 3.2rem;
  line-height: 3.8rem;
}

@media screen and (max-width: 1199px) {
  .site-second-title {
    font-size: 2.6rem;
    line-height: 3rem;
  }
}

@media screen and (max-width: 767px) {
  .site-second-title {
    font-size: 2.5rem;
    line-height: 3rem;
  }
}

.slider-nav {
  width: 10.8rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media screen and (max-width: 767px) {
  .slider-nav {
    width: 7.8rem;
  }
}

.slider-nav.hero__slider-nav .progress {
  transition: fill 200ms;
}

.slider-nav.hero__slider-nav .progress:nth-child(1) {
  stroke-dasharray: 135;
  stroke-dashoffset: 135;
}

.slider-nav.js-anim-start .progress:nth-child(1) {
  animation: circle 9s linear infinite;
  animation-delay: 0.1s;
}

.slider-nav__circle {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.slider-nav__circle .progress {
  transition: stroke 200ms;
}

.slider-nav__circle .progress:nth-child(1) {
  stroke-dasharray: 135;
  stroke-dashoffset: 0;
}

@keyframes circle {
  from {
    stroke-dashoffset: 135;
  }
  to {
    stroke-dashoffset: 0;
  }
}

.slider-nav__btn {
  position: relative;
  width: 4.4rem;
  height: 4.4rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid transparent;
  cursor: pointer;
  transition: background-color 200ms, border-color 200ms;
}

.slider-nav__btn:hover .progress:nth-child(1) {
  stroke: var(--color-orange);
}

.slider-nav__btn:hover .slider-nav__btn-icon {
  fill: var(--color-orange);
}

.slider-nav__btn:active {
  transform: scale(0.9);
}

.slider-nav__btn.swiper-button-disabled {
  opacity: 0.5;
  pointer-events: none;
  border-color: rgba(45, 48, 64, 0.1);
}

.slider-nav__btn.swiper-button-disabled .slider-nav__circle {
  opacity: 0;
}

.slider-nav__btn-icon {
  display: block;
  width: 2rem;
  height: 2rem;
  fill: var(--color-dark);
  transition: fill 200ms;
}

@media screen and (max-width: 767px) {
  .slider-nav__btn {
    width: 3.5rem;
    height: 3.5rem;
  }
  .slider-nav__btn-icon {
    width: 1.5rem;
    height: 1.5rem;
  }
}

.button {
  position: relative;
  z-index: 1;
  display: inline-block;
  padding: 2.3rem 4.3rem;
  border-radius: 1.5rem;
  border: 2px solid var(--color-orange);
  color: var(--color-orange);
  font-weight: 900;
  font-size: 1.5rem;
  line-height: 2rem;
  text-align: center;
  letter-spacing: 0.11em;
  text-transform: uppercase;
  transition: color 400ms;
  overflow: hidden;
  cursor: pointer;
}

.button::before {
  position: absolute;
  top: 100%;
  left: 50%;
  z-index: -1;
  width: 7rem;
  height: 7rem;
  background-color: var(--color-orange);
  border-radius: 50%;
  transform: translate(-50%);
  transition: all 400ms;
  content: "";
}

.button_secondary {
  background-color: var(--color-orange);
  color: var(--color-white);
  text-transform: none;
}

.button_secondary::before {
  background-color: var(--color-orange-dark);
}

@media screen and (max-width: 767px) {
  .button {
    font-size: 1rem;
    line-height: 1.5rem;
    padding: 1.9rem 3.4rem;
  }
}

@media screen and (min-width: 767px) {
  .button:hover {
    color: var(--color-white);
  }
  .button:hover::before {
    width: 55rem;
    height: 55rem;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}

.button:active {
  transform: scale(0.99);
}

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-white);
  z-index: 19;
  transform: opacity 200ms, visibillty 200ms;
}

.preloader._is-loaded {
  opacity: 0;
  visibility: hidden;
}

.preloader__image {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 7rem;
  height: 7rem;
  transform: translate(-50%, -50%);
  animation: preloader-rotate 3s infinite linear;
  color: var(--color-orange);
}

@keyframes preloader-rotate {
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

.btn-top-container {
  position: fixed;
  left: 0;
  bottom: 4rem;
  width: 100%;
  opacity: 0;
  visibility: hidden;
  transition: opacity 200ms, visibility 200ms;
}

.btn-top-container .btn-top {
  margin-left: auto;
}

.btn-top-container.is-active {
  opacity: 1;
  visibility: visible;
}

@media screen and (max-width: 1199px) {
  .btn-top-container {
    display: none;
  }
}

.btn-top {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.btn-top:hover ._icon-wrap {
  background-color: var(--color-orange);
}

.btn-top:hover ._icon-wrap .footer__btn-top-icon {
  fill: var(--color-orange-light);
}

.btn-top:hover ._name {
  color: var(--color-orange-light);
}

.btn-top ._name {
  font-weight: 700;
  color: var(--color-orange);
  font-size: 1.2rem;
  line-height: 1.6rem;
  text-transform: uppercase;
  transition: color 200ms;
}

.btn-top ._icon-wrap {
  width: 5.8rem;
  height: 5.8rem;
  background-color: var(--color-orange-light);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 1.5rem;
  transition: background-color 200ms;
}

.btn-top-icon {
  width: 2.7rem;
  height: 2.7rem;
  fill: var(--color-orange);
  transition: fill 200ms;
}

.markweber {
  display: block !important;
}

.markweber img {
  transform: translateY(0.2rem);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

